const _this = this
export default {
	data() {
		return {
			user: {}
		}
	},
	onLoad() {
		const user = uni.getStorageSync('user')
		if (user) {
			this.user = user
		}
	},
	methods: {
		loginClick(e) {
			this.user = e.detail.userInfo
			uni.setStorageSync('user', e.detail.userInfo);
		}
	}
}
